<template>
  <div>
    <el-table :data="students" border>
      <el-table-column label="学生编号" prop="id"></el-table-column>
      <el-table-column label="学生姓名" prop="name"></el-table-column>
      <el-table-column label="学生班级" prop="class"></el-table-column>
      <el-table-column label="操作">
        <template #default="{ row }">
          <el-button type="primary" @click="openDialog(row)">提问</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog v-model="dialogVisible" :title="dialogTitle" width="400px">
      <div class="dialog-content" style="width: 350px; height: 300px" ref="dialogContent">
        <div v-for="message in messages" :key="message.id" :class="['message', message.from]">
          {{ message.content }}
        </div>
      </div>
      <div class="dialog-footer">
        <el-input v-model="inputMessage" placeholder="请输入内容"></el-input>
        <div class="footer-buttons">
          <el-button type="primary" @click="sendMessage">发送</el-button>
          <el-button type="primary" @click="saveChatData">保存</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { ref, nextTick, watch } from 'vue' // 导入 watch 函数
import { ElTable, ElTableColumn, ElButton, ElDialog, ElInput, ElMessage } from 'element-plus'

export default {
  components: {
    ElTable,
    ElTableColumn,
    ElButton,
    ElDialog,
    ElInput
  },
  setup() {
    const students = ref([
      { id: 1, name: '张三', class: '一年级' },
      { id: 2, name: '李四', class: '二年级' },
      { id: 3, name: '王五', class: '三年级' },
      // 添加更多学生数据...
    ])

    const dialogVisible = ref(false)
    const dialogTitle = ref('')
    const messages = ref([])
    const inputMessage = ref('')
    const dialogContentRef = ref(null)

    const openDialog = (student) => {
      dialogTitle.value = `${student.name}（编号：${student.id}）`
      messages.value = []
      inputMessage.value = ''
      dialogVisible.value = true
    }

    const sendMessage = () => {
      const message = {
        id: messages.value.length + 1,
        from: 'student',
        content: inputMessage.value
      }
      messages.value.push(message)
      inputMessage.value = ''
    }

    const saveChatData = () => {
      const saveUrl = '/api/save_chat'

      // 发送保存请求
      // 假设保存的数据是一个包含所有消息的数组
      const chatData = messages.value.map(message => ({
        from: message.from,
        content: message.content
      }))

      // 发送保存请求
      // 这里使用了 fetch API 发送 POST 请求，你也可以使用其他库或方法
      fetch(saveUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(chatData)
      })
        .then(response => response.json())
        .then(data => {
          if (data.success) {
            ElMessage.success('聊天记录保存成功')
          } else {
            ElMessage.error('保存失败，请重试')
          }
        })
        .catch(error => {
          console.error('保存失败:', error)
          ElMessage.error('保存失败，请重试')
        })
    }

    // 监听对话框内容的变化，使滚动条自动滚动到底部
    const scrollToBottom = () => {
      nextTick(() => {
        const dialogContent = dialogContentRef.value
        if (dialogContent) {
          dialogContent.scrollTop到底部
        }
      })
    }

    // 监听消息数组的变化，执行滚动到底部的操作
    watch(messages, scrollToBottom)

    return {
      students,
      dialogVisible,
      dialogTitle,
      messages,
      inputMessage,
      dialogContentRef,
      openDialog,
      sendMessage,
      saveChatData
    }
  }
}
</script>

<style scoped>
.dialog-content {
  overflow-y: auto;
  padding: 10px;
}

.message {
  margin-bottom: 10px;
  padding: 8px;
  border-radius: 5px;
  word-break: break-all; 
}

.student {
  background-color: #e6f7ff;
}

.teacher {
  background-color: #f0f0f0;
}

.dialog-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.footer-buttons {
  display: flex;
  gap: 10px;
}
</style>